<template>
    <div class="bg">
        <div class="titleName">
            <img src="../../images/369logo.png" alt="">
        </div>
        <div class="colorText">
            <p><img src="../../images/registGift.png" alt=""></p>
            <p class="text01"><img src="../../images/colorText01.png" alt=""></p>
            <p class="text02"><img src="../../images/colorText02.png" alt=""></p>
        </div>
        <div class="button">
            <span>微信授权登录</span>
        </div>
        <div class="footer">
            <p>成为注册用户后，您也可以成为我们的推广，邀请更多好友来，和您一起游戏</p>
        </div>
    </div>
</template>

<script>
export default {
}
</script>

<style lang="less" scoped>
.bg{
    height:100%;
    background: url("../../images/share_bg.png") repeat top center;
    background-size: 100% 100%;
    text-align: center;
    .titleName{
        padding-top:16px;
        img{
            width:54%;
        }
    }
    .colorText{
        margin-top:20px;
        img{
            display: inline-block;
            width:35%;
        }
        .text01>img{
            margin-top:30px;
            width:85%;
        }
        .text02>img{
            margin-top:30px;
            width:50%;
        }
    }
    .button{
        margin:0 auto;
        margin-top:34%;
        span{
            display: inline-block;
            width:48%;
            height:1.6rem;
            color:#fff;
            line-height: 1.6rem;
            font-size:.5rem;
            border-radius: 0.4em;
            background-color: #288CEA;
        }
    }
    .footer{
        margin:0 auto;
        margin-top:20%;
        width:90%;
        font-size:.38rem;
    }
}
</style>



